@import "./src/common/styles/sass/variables.scss";

.container{
    height: 100%;
    width: 80%;
    max-width: $maxDisplayWidth;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
@media screen and (max-width: 777px){
    .container {
        justify-content: center;
    }
}
